import React from 'react';
import insertRule from './sheet';
import { useCache } from './style-cache';
import { isNodeEnvironment } from './is-node';
import { sortCssRules } from './sort-media-queries';

function Style(props) {
  var inserted = useCache();
  var sheets = React.useRef([]).current;

  if (props.children.length) {
    if (false) {
      for (var i = 0; i < props.children.length; i++) {
        var sheet = props.children[i];

        if (inserted[sheet]) {
          continue;
        } else {
          inserted[sheet] = true;
          sheets.push(sheet);
        }
      }

      if (!sheets.length) {
        return null;
      }

      return React.createElement("style", {
        "data-s-ssr": true,
        nonce: props.nonce,
        dangerouslySetInnerHTML: {
          __html: sortCssRules(sheets).join('')
        }
      });
    } else {
      for (var i = 0; i < props.children.length; i++) {
        var sheet = props.children[i];

        if (inserted[sheet]) {
          continue;
        }

        inserted[sheet] = true;
        insertRule(sheet, props);
      }
    }
  }

  return null;
}

export default Style;